html {
  padding: 0;
  margin: 0;
  background-color: #1C3738;
  height: 100%;
}
a {
  text-decoration: none;
  color: none;
  box-sizing: border-box;
}

hr {
  color: #BE996E;
  filter: drop-shadow(0 0 0.65rem rgb(206, 150, 18));
}
* {
  font-family: 'Crimson Pro', serif;
  color: #F4FFF8;
}

h1, h2, h3, h4, h5, h6, p {
  color: #F4FFF8;
}